import React from "react";
import { createRoot } from "react-dom/client";
import App from "./App.tsx";
import "./globals.css";
import './lib/i18n';

// Gestion globale des erreurs pour éviter les erreurs de console
window.addEventListener('error', (event) => {
  // Ignorer les erreurs Cloudflare bloquées par le client
  if (event.message.includes('ERR_BLOCKED_BY_CLIENT') || 
      event.filename?.includes('cloudflare')) {
    event.preventDefault();
    return;
  }
  
  // Logger les autres erreurs pour le debugging
  console.warn('Erreur interceptée:', event.error);
});

// Gestion des erreurs de ressources non chargées
window.addEventListener('unhandledrejection', (event) => {
  if (event.reason?.message?.includes('ERR_BLOCKED_BY_CLIENT')) {
    event.preventDefault();
    return;
  }
});

const rootElement = document.getElementById("root");
if (!rootElement) {
  console.error('❌ Element #root non trouvé');
  document.body.innerHTML = '<div style="padding: 20px; color: red;">Erreur: Element #root non trouvé</div>';
} else {
  try {
    createRoot(rootElement).render(<App />);
  } catch (error) {
    console.error('❌ Erreur lors du rendu:', error);
    rootElement.innerHTML = 
      '<div style="padding: 20px; font-family: Arial, sans-serif; max-width: 600px; margin: 50px auto;">' +
        '<h1 style="color: #dc2626;">Erreur de rendu React</h1>' +
        '<p>L\'application n\'a pas pu se charger correctement.</p>' +
        '<details style="margin-top: 20px;">' +
          '<summary>Détails de l\'erreur</summary>' +
          '<pre style="background: #f3f4f6; padding: 10px; border-radius: 4px; overflow: auto;">' + error + '</pre>' +
        '</details>' +
        '<button onclick="location.reload()" style="margin-top: 20px; padding: 10px 20px; background: #2563eb; color: white; border: none; border-radius: 4px; cursor: pointer;">' +
          'Recharger la page' +
        '</button>' +
      '</div>';
  }
}