import React, { Component, ErrorInfo, ReactNode } from 'react';

interface Props {
  children: ReactNode;
  fallback?: ReactNode;
}

interface State {
  hasError: boolean;
  error?: Error;
}

class ErrorBoundary extends Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError(error: Error): State {
    return { hasError: true, error };
  }

  componentDidCatch(error: Error, errorInfo: ErrorInfo) {
    // Filtrer les erreurs Cloudflare et autres erreurs non critiques
    if (error.message.includes('ERR_BLOCKED_BY_CLIENT') || 
        error.message.includes('cloudflare') ||
        errorInfo.componentStack.includes('cloudflare')) {
      console.warn('Erreur Cloudflare ignorée:', error.message);
      return;
    }
    
    console.error('ErrorBoundary caught an error:', error, errorInfo);
    
    // En production, envoyer l'erreur à un service de monitoring si nécessaire
    if (process.env.NODE_ENV === 'production') {
      // Ici vous pourriez envoyer l'erreur à Sentry, LogRocket, etc.
      console.warn('Erreur en production - considérez l\'ajout d\'un service de monitoring');
    }
  }

  render() {
    if (this.state.hasError) {
      return this.props.fallback || (
        <div className="flex items-center justify-center min-h-screen bg-background">
          <div className="text-center space-y-4">
            <h1 className="text-2xl font-bold text-destructive">Une erreur est survenue</h1>
            <p className="text-muted-foreground">
              L'application a rencontré un problème. Veuillez rafraîchir la page.
            </p>
            <button
              onClick={() => window.location.reload()}
              className="px-4 py-2 bg-primary text-primary-foreground rounded-md hover:bg-primary/90"
            >
              Rafraîchir
            </button>
            {process.env.NODE_ENV === 'development' && this.state.error && (
              <details className="mt-4 text-left">
                <summary className="cursor-pointer text-sm text-muted-foreground">
                  Détails de l'erreur (dev)
                </summary>
                <pre className="mt-2 p-4 bg-muted rounded text-xs overflow-auto">
                  {this.state.error.stack}
                </pre>
              </details>
            )}
          </div>
        </div>
      );
    }

    return this.props.children;
  }
}

export default ErrorBoundary;
